.search {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 1s ease all;
    z-index: 1;
  }

.show {
  width: 100%;
  height: 100%;
  background: #ccc;
  transform: translateX(0);
  
}

.hide{
    transform: translateX(-200%);
}

.search-city{
  width: 100%;
  height: 40%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.Search__dialog {
  font-size: 5vmin;
  border: none;
  padding: 3%;
}

.search__btn {
  font-size: 5vmin;
  padding: 3%;
  border: none;
  background: #bbb;
}

.search__title{
  font-size: 4vmax;
  margin-bottom:2rem ;
  color: #000;
}