.menu {
  width: 10%;
  height: 0.75%;
  background: #fff;
  position: absolute;
  top: 5%;
  left: 5%;
  transition: 0.7s ease all;
  z-index: 2;
}

.menu::after {
  content: "";
  width: 80%;
  height: 100%;
  background: #fff;
  top: 200%;
  position: absolute;
}

.close.menu{
  background: #000;
  transform: rotate(-45deg);
}

.close.menu::after{
  width: 100%;
  height: 100%;
  background: #000;
  transform: rotate(90deg) translate(-25%,-25%);
}
