.hours-weather {
  position: absolute;
  top: 0;
  left: 0;
  background: #111;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateX(100%);
  transition: 1s ease all;
}

.show{
  transform: translateX(0);
}

.hour-weather {
  margin-top: 20vh;
}

.one-hour {
  text-align: center;
  font-size: 2vmax;
  padding: 10% 0;
  position: relative;
}

.one-hour::after {
  content: "";
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  bottom: 5%;
  left: 0;
}

.close{
  position: absolute;
  top: 5%;
  right: 5%;
  font-size: 3vmax;
}